import React from 'react';
import {render} from 'react-dom';
import createPConnectComponent from './bridge/react_pconnect';
import StoreContext from "./bridge/Context/StoreContext";

import './css/sdkCssVars.css';
import './css/sdkStyles.css';

export default function initialRender(inRenderObj, containerId?) {
    const {
      props,
      domContainerID = null,
      componentName,
      portalTarget,
      styleSheetTarget
    } = inRenderObj;
  
    let target: any = null;
  
    if (domContainerID) {
      target = document.getElementById(domContainerID);
    } else if (portalTarget) {
      target = portalTarget;
    } else if (containerId) {
      target = document.getElementById(containerId);
    }
  
    const Component: any = (rootProps) => {
      const PegaConnectObj = createPConnectComponent();
  
      const thePConnObj = <PegaConnectObj {...rootProps} />;
  
      // NOTE: For Embedded mode, we add in displayOnlyFA and isMashup to our React context
      //  so the values are available to any component that may need it.
      const theComp =
        <StoreContext.Provider value={{ store: window.PCore.getStore(), displayOnlyFA: true, isMashup: true }} >
          {thePConnObj}
        </StoreContext.Provider>;
  
      return theComp;
    };
  
    if (componentName) {
      Component.displayName = componentName;
    }
  
    const theComponent = (
        <Component
          {...props}
          portalTarget={portalTarget}
          styleSheetTarget={styleSheetTarget}
        />
    );
  
    render(
      <React.Fragment>

        {theComponent}
      </React.Fragment>,
      target
    )
  }
  